.online_content{
      align-items: center;
      height: 2rem;
      gap: .75rem;
}

.online_content span{
      color: var(--text-200);
      text-transform: uppercase;
      font-size: 1.3rem;
      display: flex;
      padding-top: .25rem;
}

.online_dot{
      width: 1.4rem;
      height: 1.4rem;
      border-radius: 50%;
}

.online_dot.green{
      background: var(--green);
}

.online_dot.red{
      background: var(--red);
}