.wrapper_top{
      width: 100%;
      height: 80rem;
      position: relative; 
}

.top_back{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
}

.top_back__banner{
      width: 100%;
      height: 100%;
      overflow: hidden;
      position: relative;
}

.top_back__banner:before{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: var(--b-gradient);
}

.top_front{
      width: 100%;
      height: 100%;
      position: relative;
      padding-top: 8rem;
}

.top_front__header{
      width: 100%;
      height: 12.2rem;
      align-items: center;
      justify-content: space-between;
}

.header_logo{
      width: 20.9rem;
}

.header_user{
      width: 27rem;
      height: 7rem;
      position: relative;
      align-items: center;
      gap: 2rem;
}

.header_user__data{
      flex-direction: column;
      padding: 1rem;
      width: 14rem;
      height: 6.4rem;
      background: var(--bg-100);
      border-radius: .6rem;
      align-items: flex-end;
      position: relative;
      flex-shrink: 0;
      transform: translateY(-1rem);
      animation: move_up__down 1.2s ease-in-out infinite;
}

@keyframes move_up__down{
      0%, 100%{
            transform: translateY(-1rem);
      }
      50%{
            transform: translateY(1rem);
      }
}

.header_user__data:before{
      width: 1.4rem;
      height: 1.4rem;
      background: var(--bg-100);
      transform: rotate(45deg);
      top: calc(50% - .7rem);
      right: -.7rem;
}

.header_user__data strong{
      font-family: var(--ft-title-4);
      font-weight: 400;
      font-size: 2rem;
      color: var(--text-200);
}


.header_user__avatar{
      width: 6.2rem;
      height: 6.2rem;
      box-shadow: 0 0 0 .3rem var(--bg-100);
      flex-shrink: 0;
      border-radius: 50%;
      overflow: hidden;
}

.btn_toggle{
      width: 3rem;
      height: 3rem;
      flex-shrink: 0;
      border-radius: 50%;
      background: transparent;
      transition: .3s ease-in-out;
}

.btn_toggle.rotate{
      transform: rotate(180deg);
}

.btn_toggle *{
      font-size: 2.5rem;
      color: var(--text-200);
}


.header_dropdown__menu{
      position: absolute;
      top: 110%;
      right: -2.5rem;
      width: 28.5rem;
      padding: 2.5rem;
      background: var(--bg-200);
      border-radius: 1rem;
      opacity: 0;
      visibility: hidden;
      transform: translateY(5rem);
      transition: .5s ease-in-out;
}

.header_dropdown__menu.active{
      opacity: 1;
      visibility: visible;
      transform: translateY(1rem);
}

.header_dropdown__menu:before{
      width: 2rem;
      height: 2rem;
      background: inherit;
      transform: rotate(45deg);
      top: -1rem;
      right: 2.2rem;
}

.dropdown_married{
      width: 100%;
      height: 6rem;
      gap: 1rem;
      cursor: pointer;
}

.dropdown_avatar{
      width: 6rem;
      height: 6rem;
      position: relative;
      background: var(--bg-300);
      padding: .4rem;
      border-radius: 50%;
}

.dropdown_avatar__img{
      width: 5.2rem;
      height: 5.2rem;
      border-radius: 50%;
      overflow: hidden;
}

.dropdown_icon{
      width: 2.4rem;
      height: 2.4rem;
      position: absolute;
      background: var(--bg-300);
      border-radius: 50%;
      bottom: 0;
      right: -.3rem;
      padding: .1rem 0 0 .1rem;
}

.dropdown_icon *{
      color: var(--red);
      font-size: 1.4rem;
}

.dropdown_info span{
      font-size: 2rem;
}

.dropdown_info h4{
      font-weight: 400;
      font-size: 2rem;
      font-family: var(--ft-title-3);
}

.dropdown_navigator{
      margin-top: 2.5rem;
      padding-top: 2.5rem;
      position: relative;
}

.dropdown_navigator::before{
      width: 100%;
      height: .1rem;
      top: 0;
      left: 0;
      background: var(--v-gradient);
}

.dropdown_navigator ul li a{
      width: 100%;
      gap: 1rem;
      align-items: center;
      background: var(--bg-300);
      height: 4rem;
      margin-bottom: .5rem;
      padding: 0 2rem;
      border-radius: .4rem;
}

.dropdown_navigator ul li a *{
      color: var(--pink-100);
}

.dropdown_navigator ul li a span{
      color: var(--text-200);
}

.top_front__main{
      height: calc(100% - 15rem);
      flex-direction: column;
      align-items: center;
}

.user_title{
      margin-top: 7rem;
      width: 80%;
      justify-content: center;
}

.user_title h1{
      font-size: 8rem;
      text-transform: capitalize;
      text-align: center;
      line-height: 7.5rem;
}

.user_description{
      margin-top: 4rem;
      width: 80%;
}

.user_description p{
      font-size: 2rem;
      text-align: center;
      white-space: pre-line;
}

.user_buttons{
      margin-top: 3rem;
      flex-direction: column;
}

.user_links{
      width: 100%;
      gap: 2rem;
      margin-top: 3rem;
      justify-content: space-around;
      align-items: center;
}

.user_links a{
      gap: 1rem;
      align-items: center;
}

.user_links a img{
      width: 3.5rem;
}

.user_links span{
      font-size: 2.5rem;
      color: var(--text-200);
      font-family: var(--ft-title-4);
}