.wrapper_relation{
      width: 100%;
      height: 100vh;
      min-height: 92rem;
      position: fixed;
      top: 0;
      left: 0;
      background: rgba(0,0,0,.8);
      opacity: 0;
      visibility: hidden;
      transition: .5s;
}

.wrapper_relation.active{
      opacity: 1;
      visibility: visible;
}

.relation_content{
      width: 55rem;
      height: 65rem;
      background: var(--bg-300);
      border-radius: 1rem;
      border: dashed .1rem var(--border);
      overflow: hidden;
      position: relative;
      transform: translateY(30%);
      opacity: 0;
      transition: .3s .5s ease-out;
}

.wrapper_relation.active .relation_content{
      opacity: 1;
      transform: translateY(0);
}

.relation_bg{
      width: 100%;
      height: 100%;
      position: absolute;
      overflow: hidden;
}

.relation_bg:before{
      width: 36rem;
      height: 36rem;
      background: var(--h-gradient);
      border-radius: 50%;
      top: 0;
      left: 0;
      transform: translate(-50%, -10%);
}

.relation_bg:after{
      width: 19.5rem;
      height: 19.5rem;
      background: rgba(255,255,255,.3);
      backdrop-filter: blur(10px);
      border-radius: 50%;
      top: 0;
      right: 0;
      transform: translate(30%, -20%);
      z-index: 10;
}

.relation_circle{
      width: 8rem;
      height: 8rem;
      position: absolute;
      background: var(--pink-100);
      transform: translate(470%, 20%);
}

.relation_circle,
.relation_circle:before,
.relation_circle:after{
      border-radius: 50%;
      top: 0;
      left: 0;
}

.relation_circle:before{
      width: 6.5rem;
      height: 6.5rem;
      background: var(--purple-100);
      transform: translate(120%, 160%);
}

.relation_circle:after{
      width: 3rem;
      height: 3rem;
      background: var(--text-200);
      transform: translate(70%, 300%);
}

.relation_front{
      position: relative;
      width: 100%;
      height: 100%;
      padding: 2.5rem;
}

.relation_front__header{
      margin-top: 8.5rem;
      width: 100%;
      height: auto;
}

.relation_avatar{
      width: 100%;
      height: 17rem;
      align-items: center;
      gap: 2rem;
}

.relation_avatar__img{
      width: 17rem;
      height: 17rem;
      border-radius: 50%;
      border: solid .5rem var(--bg-300);
      overflow: hidden;
}

.relation_type{
      width: 15rem;
      height: 5rem;
      background: var(--bg-100);
      align-items: center;
      padding: 0 1.5rem;
      position: relative;
      gap: .5rem;
      border-radius: .4rem;
}

.relation_type:before{
      width: 1.6rem;
      height: 1.6rem;
      background: inherit;
      transform: rotate(45deg);
      left: -.8rem;
      top: calc(50% - .8rem);
}

.relation_type__icon{
      width: 3rem;
      height: 3rem;
      animation: pulse .5s ease-in-out infinite;
      position: relative;
}

.relation_type__icon *{
      color: var(--red);
      font-size: 2.5rem;
}

@keyframes pulse{
      0%{
            transform: scale(.4);
      }
      100%{
            transform: scale(1);
      }
}

.relation_type span{
      font-family: var(--ft-title-4);
      font-size: 2rem;
}

.relation_data{
      margin-top: 1rem;
      padding-left: 4rem;
      align-items: first baseline;
      gap: 2rem;
}

.relation_data__info{
      flex-direction: column;
}

.relation_data__info h2{
      font-size: 3.5rem;
}

.relation_data__info strong{
      font-size: 2.5rem;
}

.relation_data__info a{
      color: var(--text-200);
      font-size: 1.8rem;
}

.relation_data__links{
      align-items: center;
      gap: 2rem;
}

.relation_data__links a{
      width: 11.8rem;
      height: 4rem;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: .4rem;
      color: var(--text-200);
}

.relation_data__links a:first-child{
      background: var(--v-gradient);
      padding: .1rem;
}

.relation_data__links a:first-child span{
      background: var(--bg-300);
      width: 100%;
      height: 100%;
      text-align: center;
      line-height: 3.8rem;
      border-radius: .4rem;
}

.relation_data__links a:last-child{
      background: var(--bg-300);
      border: dashed .1rem var(--border);
}

.relation_description{
      margin-top: 3rem;
}

.relation_description p{
      white-space: pre-line;
      font-size: 1.4rem;
}

.btn_close{
      position: absolute;
      width: 4.6rem;
      height: 4.6rem;
      bottom: 0;
      right: 0;
      background: var(--purple-100);
}

.btn_close *{
      color: var(--light);
      font-size: 2.4rem;
}