.wrapper_footer{
      border-bottom: solid 5rem var(--bg-300);
}

.footer_credits{
      width: 100%;
      height: 8rem;
      position: relative;
}

.footer_credits:before{
      width: 100%;
      height: .1rem;
      background: var(--v-gradient);
      left: 0;
      bottom: 0;
}

.footer_credits strong{
      color: var(--text-200);
      font-family: var(--ft-title-4);
      font-size: 2rem;
      font-weight: 500;
}

.footer_links{
      height: 6rem;
      gap: 3rem;
}

.footer_links a{
      color: var(--text-200);
      font-style: italic;
      font-size: 1.4rem;
      text-decoration: underline;
}