.wrapper_themes{
      margin-top: 12rem;
}

.title_section{
      flex-direction: column;
      align-items: center;
      width: 100%;
}

.title_section p{
      text-transform: uppercase;
      letter-spacing: .1rem;
}

.title_section h1{
      font-size: 7rem;
}

.wrapper_themes{
      gap: 2.5rem;
}

.card_theme{
      width: 100%;
      height: auto;
      border-radius: 1rem;
      overflow: hidden;
      border: dashed .1rem var(--border);
      background: var(--bg-300);
      box-shadow: 2rem 2rem 20px rgba(0,0,0,.8);
}

.card_theme__image{
      width: 100%;
      height: 32rem;
      position: relative;
      overflow: hidden;
}

.card_theme__image_back,
.card_theme__image_front{
      width: 100%;
      height: 100%;
}

.card_theme__image_front{
      position: absolute;
      top: 0;
      left: 0;
      background: linear-gradient(rgba(68,68,68, 0), var(--bg-300));
}

.card_theme__new{
      width: 100%;
      height: 4.5rem;
      background: var(--bg-300);
      border: dashed .1rem var(--border);
      top: 0;
      left: 0;
      transform: rotate(-45deg) translate(-31.5%, -175%);
}

.card_theme__new span{
      font-family: var(--ft-title-4);
      font-size: 2rem;
      color: var(--text-200);
}

.card_theme__logo{
      position: absolute;
      width: 10rem;
      left: 2rem;
      bottom: 1rem;
}

.card_theme__data{
      width: 100%;
      height: auto;
      padding: 2rem;
      position: relative;
      margin-top: 1rem;
}

.card_theme__data:before{
      width: calc(100% - 4rem);
      height: .1rem;
      top: 0;
      left: 2rem;
      background: var(--v-gradient);
}

.theme_data__info{
      width: 100%;
      height: auto;
      align-items: center;
      justify-content: space-between;
}

.data_info__model,
.data_info__views{
      flex-direction: column;
      align-items: flex-start;
}

.data_info__model span,
.data_info__views span{
      text-transform: uppercase;
      font-size: 1.4rem;
}

.data_info__model h2{
      font-size: 3.5rem;
      font-weight: 500;
}

.data_info__views strong{
      font-size: 3rem;
      color: var(--text-200);
      font-weight: 800;
}

.theme_data__link{
      margin-top: 2rem;
}

.theme_data__link .btn_princ{
      width: 20rem;
      height: 5.5rem;
}

.theme_data__link .btn_princ .btn_princ__title{
      height: 4.8rem;
}

.wrapper_themes__all_links{
      height: 34rem;
}