@import url('https://fonts.googleapis.com/css2?family=Candal&family=Coda:wght@400;800&family=Days+One&family=Jura:wght@300..700&family=Nanum+Gothic&family=Racing+Sans+One&family=Rubik+Mono+One&family=Smooch+Sans:wght@100..900&family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&family=Squada+One&family=Unica+One&display=swap');

:root{
    /** fonts */
    --ft-txt-1: "Jura", sans-serif;
    --ft-txt-2: "Smooch Sans", sans-serif;
    --ft-txt-3: "Nanum Gothic Coding", monospace;
    --ft-txt-4: "Space Mono", monospace;
    --ft-txt-5: "Coda", system-ui;
    --ft-txt-6: "Unica One", sans-serif;
    --ft-title-1: "Candal", sans-serif;
    --ft-title-2: "Rubik Mono One", monospace;
    --ft-title-3: "Days One", sans-serif;
    --ft-title-4: "Racing Sans One", sans-serif;
    --ft-title-5: "Squada One", sans-serif;


    /** theme */
    --pink-100: #d212bb;
    --pink-200: #991ed2;
    --purple-400: #5d21c1;
    --purple-300: #6e28e4;
    --purple-200: #991ed2;
    --purple-100: #6e28e4;
    --v-gradient: linear-gradient(90deg, var(--pink-100), var(--purple-100));
    --t-gradient: linear-gradient(90deg, var(--pink-100), var(--purple-100), var(--pink-100), var(--purple-100));
    --h-gradient: linear-gradient(135deg, var(--pink-100), var(--purple-400));
    --btn-gradient: radial-gradient(15deg, transparent 35%, var(--light) 100%);
    --b-gradient: linear-gradient(rgba(10,10,10,.53), var(--bg-400));
    
    /** colors */
    --bg-400: #0a0a0a;
    --bg-300: #0e0e0e;
    --bg-200: #2a2a2a;
    --bg-100: #3b424d;
    --text-200: #efe2ff;
    --text-100: #fff;
    --border: #494949;
    --light: #ffffff;
    --green: #74e25b;
    --red: #d90000;

    /** social color*/
    --facebook: #3b5995;
    --twitter: #171717;
    --google: #dd4b39;
    --youtube: #c4302b;
    --vimeo: #1ab7ea;
    --dribbble: #ea4c89;
    --github: #333;
    --instagram: #c13584;
    --pinterest: #e60023;
    --flickr: #0063dc;
    --bitbucket: #205081;
    --linkedin: #0e76a8;
    --whatsapp: #12af0a;
    --tumblr: #35465c;
    --tiktok: #222;
    --discord: #5865f2;
    --twitch: #9146ff;
    
    /** globals */
    font-family: var(--ft-txt-5);
    font-size: 62.5%;

}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: auto;
    min-height: 100vh;
    overflow-x: hidden;
    background: var(--bg-400);
    color: var(--text-100);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body *{
    font-size: 1.6rem;
    font-family: var(--ft-txt-5);
}

:before,
:after{
    content: '';
    position: absolute;
}

a, span{
    display: inline-block;
}

a{
    text-decoration: none;
}

img{
    widtH: 100%;
    object-fit: cover;
}

ul, ol{
    list-style: none;
}

h1, h2, h3, h4{
    line-height: 120%;
    font-family: var(--ft-title-4);
}

button{
    border: none;
    outline: none;
    cursor: pointer;
}

.flex{
    display: flex;
}

.center{
    justify-content: center;
    align-items: center;
}

.grid{
    display: grid;
}

.col-2{
    grid-template-columns: repeat(2, 1fr);
}

.col-3{
    grid-template-columns: repeat(3, 1fr);
}

.col-4{
    grid-template-columns: repeat(4, 1fr);
}

.container{
    width: 96%;
    max-width: 120rem;
    height: auto;
    position: relative;
    gap: 2.5rem;
    margin: 0 auto;
}

@property --a{
    syntax: '<angle>';
    inherits: false;
    initial-value: 0deg;
}

.effect{
    position: relative;
    width: fit-content;
    height: fit-content;
    overflow: hidden;
}

.effect:before{
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: repeating-conic-gradient(from var(--a), var(--primary) 0%, var(--primary) 5%, transparent 5%, transparent 40%, var(--primary) 50%);
    animation: rotating 4s linear infinite;
}

.title-g{
   width: fit-content;
   height: fit-content;
   color: transparent;
   background: var(--t-gradient);
   -webkit-background-clip: text;
   -moz-background-clip: text;
   background-clip: text;
   background-size: 300%;
   background-position: right top;
   animation: title_move 2s linear infinite;
}

@keyframes title_move{
    0%{
        background-position: right top;
    }
    100%{
        background-position: left top;
    }
}


.effect:after{
    background: var(--bg-200);
    top: .2rem;
    left: .2rem;
}

@keyframes rotating{
    0%{
        --a: 0deg;
    }
    100%{
        --a: 360deg;
    }
}

.btn_princ{
    width: 40rem;
    height: 7rem;
    border-radius: .6rem;
    background: var(--purple-400);
    border: solid .1rem var(--purple-400);
    box-shadow: 1rem 1rem 20px rgba(0,0,0,.2);
}

.btn_princ__title{
    width: 100%;
    height: 6.2rem;
    background: var(--purple-300);
    position: relative;
    overflow: hidden;
    border-radius: .5rem;
}

.btn_princ__title:before{
    width: 10rem;
    height: 10rem;
    background-color: var(--pink-100);
    border-radius: 50%;
    filter: blur(30px);
    opacity: .8;
    position: absolute;
    top: calc(50% - 5rem);
    left: -5rem;
    animation: move_effect 8s linear infinite;
}

@keyframes move_effect{
    0%, 100%{
        left: -5rem;
    }
    50%{
        left: calc(100% - 5rem);
    }
}

.btn_princ__title span{
    position: relative;
    color: var(--text-200);
    font-family: var(--ft-title-4);
    font-weight: 400;
    font-size: 2.6rem;
    letter-spacing: .1rem;
}

.scroll-y{
    width: 100%;
    height: 42.5rem;
    overflow-y: auto;
    scrollbar-width: thin;
    scrollbar-color: var(--bg-200), var(--purple-100);
}

.scroll-x{
    width: calc(100% - 13.6rem);
    overflow-x: auto;
    scrollbar-width: thin;
    scrollbar-color: var(--bg-200), var(--purple-100);
}

.scroll-y::-webkit-scrollbar,
.scroll-x::-webkit-scrollbar{
    width: .8rem;
    background: var(--bg-200);
    border-radius: .8rem;
}

.scroll-y::-webkit-scrollbar-thumb,
.scroll-x::-webkit-scrollbar-thumb{
    background: var(--purple-100);
    border-radius: .8rem;
}

::-webkit-scrollbar{
    width: .8rem;
    background: var(--bg-200);
    border-radius: .8rem;
}

::-webkit-scrollbar-thumb{
    background: var(--purple-100);
    border-radius: .8rem;
}