.wrapper_contact{
      padding: 15rem;
      width: 100%;
      height: auto;
      background: var(--h-gradient);
      position: relative;
}

.wrapper_contact::before{
      width: 100%;
      height: 20rem;
      background-image: url(../../assets/img/wave-2.svg);
      background-repeat: no-repeat;
      background-size: 100% auto;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
}

.contact_section{
      flex-direction: column;
      align-items: center;
      padding: 15rem 0;
}

.contact_title{
      gap: 3rem;
      align-items: center;
}

.contact_title *{
      font-size: 12rem;
      color: var(--text-200);
}

.contact_description{
      gap: 2rem;
      align-items: center;
      width: 80%;
      height: fit-content;
}

.contact_description p{
      font-size: 2.4rem;
      text-align: center;
}

.contact_link{
      margin-top: 4rem;
}